import React, { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import "./images.css";

function Accordion(props) {
  const [open, setOpen] = useState(false);
  const items = props.items;
  function alternate() {
    setOpen((val) => !val);
  }

  const arcVariants = {
    hidden: {
      height: 0,
    },
    visible: {
      height: '',
      // height:100,
      transition: {},
    },
    exits: {
      height: 0,
      transition: {
        type: "tween",
        when: "beforeChildren",
      },
      // opacity:0
    },
  };

  const ulVariants = {
    hidden: {},
    visible: {
      transition: {
        staggerChildren: 0.1,
      },
    },
    exit: {
      // scale:4
      when: "beforeChildren",
      staggerChildren: 0.2,
    },
  };

  const liItem = {
    hidden: {
      x: -50,
      opacity: 0,
    },
    visible: {
      x: 0,
      opacity: 1,
    },
    exit: {
      x: 100,
      opacity: 0,
      transition: {},
    },
  };

  return (
    <motion.div className="arc">
      <motion.div className="header-arc" onClick={alternate}>
        <motion.span>{props.title}</motion.span>
      </motion.div>

      <AnimatePresence>
        {open && (
          <motion.div
            className="content-arc"
            initial="hidden"
            animate="visible"
            variants={arcVariants}
            exit="exits"
          >
            <motion.ul
              animate="visible"
              initial="hidden"
              variants={ulVariants}
              //   exit="exit"
            >
              {
                  items.map((item, index)=>{
                      return (
                        <motion.div>
                          <motion.li className="list-item"
                          key={index}
                          variants={liItem}>
                            {item.title} <span className="amount">{item.amount}</span>
                          </motion.li>
                        </motion.div>
                      );
                  })
              }
            </motion.ul>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.div>
  );
}

export default Accordion;
