import React from 'react'
import Accordion from './Accordion'

function ServicesInfo() {
    const itemsList = [
      { title: "Insta Celeb", amount: 2300 },
      { title: "Insta Bizzness", amount: 2300 },
      { title: "Insta regular", amount: 2300 },
      { title: "Insta plus", amount: 2300 },
    ];

    const style = {
      padding: "2em 0 0 0",
      // borderBottom: "8px solid rgb(29, 27, 27)",
    };
    return (
      <div className="bg-black"
      style={style}
      >
      <p className="text-center mb-6">Available Plans</p>
        <Accordion title="Instagram plans" items={itemsList} />
        <Accordion title="Twitter plans" items={itemsList} />
        <Accordion title="SnapChat plans" items={itemsList} />
        <Accordion title="Sound Cloud plans" items={itemsList} />
      </div>
    );
}

export default ServicesInfo
