import React, { useEffect, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import "./header.css";

import { images } from "./image-data";
import Chinese from "./chinese.jpg";
import P1 from "./p1.jpg";
import P2 from "./p2.jpg";

import P110 from "./p11.jpg";
import P22 from "./p22.jpg";
import P33 from "./p33.jpg";

import P11 from "./p111.jpg";
import P222 from "./p222.jpg";
import P333 from "./p333.jpg";

function Header() {
  const [count, setCount] = useState(0);
  let index = 0;
  useEffect(() => {
    const interval = setInterval(change, 4000);
    return () => {
      clearInterval(interval);
    };
  }, []);

  const change = () => {
    index += 1;
    index = index < 3 ? index : 1;
    setCount(index);
  };

  const variants = {
    visible: {
      zIndex: 1,
      x: 0,
      opacity: 1,
    },
    hidden: {
      opacity: 0,
      x: 1000,
    },
    exit: {
    //   x: -1000,
      opacity: 0,
      zIndex: 0,
    },
  };
  return (
    <motion.div className="header">
      <motion.div className="crop">
        <AnimatePresence initial={false}>
          <motion.img
            // srcSet={`${P22} 2x,
            //   ${P33} 3x,`}
            src={images[count]}
            key={count}
            variants={variants}
            initial="hidden"
            animate="visible"
            exit="exit"
            transition={{
              x: { type: "spring", stiffness: 300, damping: 30 },
              opacity: { duration: 0.2 },
            }}
          />
        </AnimatePresence>
      </motion.div>

      <motion.div className="info">
        <div className="title">
          
            <motion.span
            initial={{
              x:100
            }}
            animate={{
              x:0
            }}
            transition={{
              duration:2,
              repeat:Infinity
            }}
            >Khloris</motion.span>
            <motion.span> Medias</motion.span>
          
        </div>
        <p className="info-p">
          Fastest growing media influencing agency in Nigeria
        </p>
      </motion.div>
    </motion.div>
  );
}

export default Header;
